/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .order-block {
    height: 100%; }
  .order-block__inner {
    flex-direction: column;
    padding-top: 98px;
    padding-left: 0; }
  .order-block__link-specification {
    display: block;
    margin-bottom: 34px; }
  .order-block__box-title {
    flex-basis: 100%; }
  .order-block__subtitle {
    margin-bottom: 12px; }
  .order-block__title {
    margin-left: 7px;
    font-size: 42px;
    line-height: 44px;
    letter-spacing: -0.1px; }
  .order-block__content-date {
    margin-bottom: 23px; }
  .order-block__link-date {
    margin-top: 2px;
    margin-left: 8px; }
  .order-block__text-date {
    margin-left: 14px; }
  .order-block__marker-box {
    max-width: 331px;
    padding-left: 5px; }
  .order-block__list {
    max-width: 350px;
    margin-bottom: 8px;
    padding-left: 7px; }
  .order-block__item {
    margin-right: 25px; }
  .order-block__box-date {
    flex-basis: 100%;
    flex-direction: column;
    align-items: flex-start; }
  .order-block__date {
    max-width: 250px;
    margin-bottom: 26px;
    margin-left: 8px; }
  .order-block__link-pay {
    margin-bottom: 0;
    margin-left: 8px; } }

